.brand-text {
  font-family: "Sriracha", cursive;
}

.absolute-top-left {
  position: absolute;
  top: 8px;
  left: 8px;
}

.horizontal-scroll {
  overflow-x: auto;
  white-space: nowrap;
  float: none;
}

.grid-container {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: auto auto;
}

@media only screen and (max-width: 900px) {
  .grid-container {
    grid-gap: 5px;
    grid-template-columns: auto;
  }
}
